body {
  background-color: lightgray;
}

.App {
  margin: 5px;
}

.App > h2 {
  text-align: center;
}

.message {
  background: white;
  padding: 5px;
  margin: 2px;
}

th, td {
  border-bottom: 1px solid #ddd;
  background-color: white;
  margin: 0;
}

.config > table th {
  color: black;
  background: burlywood;
  /* border-right: 1px solid #ddd; */
  border: 0;
}

.config > table th.row {
  word-spacing: 100vw;
}

.config > table td.table-option {
  text-align: center;
}
